import request from './request'

export const getRoles = () => {
  return request({
    url: 'roles',
    method: 'get'
  })
}
export const searchRole = (id) => {
  return request({
    url: `roles/${id}`,
    method: 'get'
  })
}
export const addRole = (data) => {
  return request({
    url: 'roles',
    method: 'post',
    data
  })
}
export const editRole = (id, data) => {
  return request({
    url: `roles/${id}`,
    method: 'put',
    data: data
  })
}
export const deleteRole = (id) => {
  return request({
    url: `roles/${id}`,
    method: 'delete'
  })
}
